.section {
  &__title {
    text-align: center;
    font-size: 32px;
    font-weight: 600;
    line-height: 1.5;
    color: $title-color;
  }

  &__subtitle {
    text-align: left;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $text-color;
  }
}
